'use client';

import { useState, useMemo, useEffect } from 'react';
import dynamic from 'next/dynamic';
import { calculateCurrentUnitPrice, calculateFinalAmount, calculateTax } from '@/lib/calculator';
import { Project } from '@/types/database';

// Dynamically import the Map component to avoid SSR issues with Leaflet
const PolygonMap = dynamic(() => import('@/components/PolygonMap'), {
  ssr: false,
  loading: () => <div className="w-full h-96 bg-slate-100 animate-pulse rounded-lg flex items-center justify-center text-slate-400">Loading Map...</div>
});

const MOCK_PROJECT: Project = {
  id: 'demo-project',
  title: 'Demonstration Campaign',
  location: 'Wakayama Minabe',
  base_price: 4600,
  min_price: 3000,
  target_area_10r: 100,
  min_target_area_10r: 50,
  max_target_area_10r: 100,
  execution_price: 4000,
  current_price: 4600
};

export default function Home() {
  const [project] = useState<Project>(MOCK_PROJECT);
  const [mapAreaSqm, setMapAreaSqm] = useState<number>(0);
  const [appliedArea10r, setAppliedArea10r] = useState<number>(0);

  // Convert sqm to 10a (1 tan = 10a = 1000sqm approx in GAS logic? No, 10a = 1000sqm. 1 tan = 10a.)
  // Wait, in `js_polygon_map.html`:
  // const areaA = areaM2 / 100; (Are)
  // const areaTan = areaM2 / 991.74; (Tan) -> This is the unit used for 10a usually in Japan agriculture contexts if strictly 1 tan.
  // But strictly 10a = 1000sqm.
  // The calculator uses "10r" (10a? or 10 R?). Probably 10a (Tan).
  // Let's use the coefficient from GAS: 991.74 for Tan conversion.
  const area10a = useMemo(() => mapAreaSqm / 991.74, [mapAreaSqm]);

  // Sync map area to applied area for simulation
  useEffect(() => {
    setAppliedArea10r(area10a);
  }, [area10a]);

  // Calculate results
  const validation = calculateCurrentUnitPrice(project, appliedArea10r);
  const currentPrice = validation.currentPrice ?? project.base_price ?? 0;
  const finalAmountExTax = calculateFinalAmount(currentPrice, appliedArea10r);
  const tax = calculateTax(finalAmountExTax);

  return (
    <main className="min-h-screen bg-slate-50 p-4 md:p-8 font-sans text-slate-800">
      <div className="max-w-6xl mx-auto space-y-6">

        {/* Header */}
        <header className="flex flex-col md:flex-row justify-between items-start md:items-center bg-white p-6 rounded-2xl shadow-sm border border-slate-100">
          <div>
            <h1 className="text-2xl md:text-3xl font-black text-slate-800 tracking-tight">
              <span className="text-green-600 mr-2">Wayfinder</span>AgriX
            </h1>
            <p className="text-slate-500 font-medium">Reverse Auction Simulator</p>
          </div>
          <div className="mt-4 md:mt-0 flex gap-3">
            <div className="px-4 py-2 bg-green-50 text-green-700 rounded-lg font-bold text-sm">
              Status: {validation.isUnformed ? 'Unformed' : 'Active'}
            </div>
          </div>
        </header>

        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">

          {/* Left Column: Map */}
          <section className="bg-white p-1 rounded-2xl shadow-sm border border-slate-100 overflow-hidden h-[500px] lg:h-[600px] relative">
            <PolygonMap
              onPolygonComplete={(coords, area) => setMapAreaSqm(area)}
            />
            {/* Overlay Stats */}
            <div className="absolute top-4 right-4 bg-white/90 backdrop-blur p-4 rounded-xl shadow-lg border border-slate-100 z-[1000] min-w-[160px]">
              <div className="text-xs font-bold text-slate-400 uppercase tracking-wider mb-1">Measured Area</div>
              <div className="text-2xl font-black text-slate-800">
                {mapAreaSqm.toFixed(0)} <span className="text-sm font-normal text-slate-500">m²</span>
              </div>
              <div className="text-sm font-bold text-green-600 mt-1">
                {area10a.toFixed(2)} <span className="text-xs text-green-500">Tan (10a)</span>
              </div>
            </div>
          </section>

          {/* Right Column: Calculator & Project Info */}
          <section className="space-y-6">

            {/* Project Details Card */}
            <div className="bg-white p-6 rounded-2xl shadow-sm border border-slate-100">
              <h2 className="text-lg font-bold text-slate-800 mb-4 flex items-center">
                <i className="w-1 h-6 bg-blue-500 rounded-full mr-3 block"></i>
                Campaign Details
              </h2>
              <div className="grid grid-cols-2 gap-4 text-sm">
                <div className="p-3 bg-slate-50 rounded-xl">
                  <div className="text-slate-500 mb-1">Location</div>
                  <div className="font-bold text-slate-800">{project.location}</div>
                </div>
                <div className="p-3 bg-slate-50 rounded-xl">
                  <div className="text-slate-500 mb-1">Target Area</div>
                  <div className="font-bold text-slate-800">{project.target_area_10r} Tan</div>
                </div>
                <div className="p-3 bg-slate-50 rounded-xl">
                  <div className="text-slate-500 mb-1">Start Price</div>
                  <div className="font-bold text-slate-800">¥{project.base_price?.toLocaleString()}</div>
                </div>
                <div className="p-3 bg-slate-50 rounded-xl">
                  <div className="text-slate-500 mb-1">Min Price</div>
                  <div className="font-bold text-slate-800 text-green-600">¥{project.min_price?.toLocaleString()}</div>
                </div>
              </div>
            </div>

            {/* Simulation Calculator */}
            <div className="bg-white p-6 rounded-2xl shadow-sm border border-slate-100 relative overflow-hidden">
              <div className="absolute top-0 left-0 w-full h-1 bg-gradient-to-r from-blue-500 to-green-500"></div>
              <h2 className="text-lg font-bold text-slate-800 mb-6 flex items-center">
                <i className="w-1 h-6 bg-green-500 rounded-full mr-3 block"></i>
                Price Simulation
              </h2>

              <div className="space-y-6">
                {/* Progress Bar */}
                <div>
                  <div className="flex justify-between text-sm mb-2">
                    <span className="font-bold text-slate-600">Progress</span>
                    <span className="font-bold text-blue-600">{(validation.progress * 100).toFixed(0)}%</span>
                  </div>
                  <div className="h-4 bg-slate-100 rounded-full overflow-hidden">
                    <div
                      className="h-full bg-gradient-to-r from-blue-400 to-blue-600 transition-all duration-500 ease-out"
                      style={{ width: `${Math.min(validation.progress * 100, 100)}%` }}
                    ></div>
                  </div>
                </div>

                {/* Input Area Override */}
                <div>
                  <label className="block text-sm font-bold text-slate-600 mb-2">Total Applied Area (Override)</label>
                  <div className="flex items-center gap-3">
                    <input
                      type="range"
                      min="0"
                      max={project.max_target_area_10r || 200}
                      step="0.1"
                      value={appliedArea10r}
                      onChange={(e) => setAppliedArea10r(Number(e.target.value))}
                      className="flex-1 h-2 bg-slate-200 rounded-lg appearance-none cursor-pointer accent-blue-600"
                    />
                    <div className="w-24 bg-white border-2 border-slate-200 rounded-lg px-3 py-2 font-mono text-right font-bold focus-within:border-blue-500 focus-within:ring-2 focus-within:ring-blue-100">
                      {appliedArea10r.toFixed(1)}
                    </div>
                    <span className="text-slate-500 font-bold">Tan</span>
                  </div>
                  <p className="text-xs text-slate-400 mt-2">* Drag slider to simulate different area totals</p>
                </div>

                <div className="border-t border-slate-100 my-6"></div>

                {/* Results */}
                <div className="bg-slate-900 rounded-2xl p-6 text-white text-center space-y-4">
                  <div>
                    <div className="text-slate-400 text-sm font-bold uppercase tracking-wider mb-1">Current Unit Price</div>
                    <div className="text-4xl font-black text-green-400 flex justify-center items-baseline gap-1">
                      <span className="text-2xl">¥</span>
                      {currentPrice?.toLocaleString()}
                      <span className="text-sm font-normal text-slate-400 ml-1">/ 10a</span>
                    </div>
                    {validation.isUnformed && <div className="text-red-400 text-xs font-bold mt-1">⚠️ Not yet formed (Below min area)</div>}
                  </div>

                  <div className="grid grid-cols-2 gap-4 border-t border-slate-700 pt-4 mt-4">
                    <div className="text-left">
                      <div className="text-slate-400 text-xs">Total (Ex. Tax)</div>
                      <div className="font-bold text-lg">¥{finalAmountExTax.toLocaleString()}</div>
                    </div>
                    <div className="text-right">
                      <div className="text-slate-400 text-xs">Total (Inc. Tax)</div>
                      <div className="font-bold text-xl text-yellow-400">¥{tax.amountInclusive.toLocaleString()}</div>
                    </div>
                  </div>
                </div>

              </div>
            </div>

          </section>
        </div>
      </div>
    </main>
  );
}
